html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  min-height: 100%; }

body {
  padding: 20px;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(30%, #FFFFFF), color-stop(#88D9FF), color-stop(#24206C), color-stop(80%, #222));
  background-image: -webkit-linear-gradient(top, #FFFFFF 30%, #88D9FF, #24206C, #222 80%);
  background-image: -o-linear-gradient(top, #FFFFFF 30%, #88D9FF, #24206C, #222 80%);
  background-image: linear-gradient(to bottom, #FFFFFF 30%, #88D9FF, #24206C, #222 80%);
  color: #333;
  background-size: 100% 400%;
  background-position: 100% 0%;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s; }



